.headerContainer {
  background-color: #000000;
  max-width: 100vw;
  height: 4.5rem;
  display: flex;
  position: relative;
  z-index: 10;
}

.headerWidth {
  width: 1170px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 30px;
  padding-left: 30px;
  margin-left: auto;
  margin-right: auto;
}

.logoContainer {
  display: flex;
  flex-direction: row;
  flex: 1;
}
.headerTitle {
  color: white;
  font-size: 20px;
  font-weight: var(--font-header-weight);
  text-decoration: none;
}
.headerAnimation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
}

.headerAnimation div {
  border: 1px solid black;
  border-radius: 30%;
  background-color: white;
  height: 10px;
  width: 10px;
}
.wave1{
  animation: waveAnimation 1s ease-in infinite;
}
.wave2{
  animation: waveAnimation 1s ease-in infinite;
  animation-delay: 0.2s;
}
.wave3{
  animation: waveAnimation 1s ease-in infinite;
  animation-delay: 0.4s;
}
@keyframes waveAnimation {
  0%, 100% {
    height: 10px;
  }
  30%, 70% {
    background-color: white;
  }
  50% {
    background-color: rgb(0, 216, 0);
    height: 15px;
  }
}




.headerOptionsRight {
  color: white; 
  display: flex;
  flex-direction: row;
  flex: 2;
  justify-content: end;
  align-items: center;
  margin-left: auto;
}

.headerOptionsRight a {
  font-size: 18px;
  font-weight: var(--font-header-weight);
  padding-right: 15px;
  padding-left: 15px;
  text-decoration: none;
  color: white;
}

.downloadSignUpBorder {
  border-right: 2px solid white;
  margin-left: 20px;
  margin-right: 20px;
  height: 1.5rem;

}